.header {
  width: 100%;
  height: 150px;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.header p {
  width: 80%;
  overflow-wrap: break-word;
  text-align: center;
  margin: auto;
}
.nav {
  display: flex;
  height: 50px;
  width: 80%;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #000;
  margin: 0 auto;
}

.nav-btn {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-basis: 30%;
}

.nav-btn li:hover {
  border-bottom: 3px solid #000;
}