.header {
  width: 100%;
  height: 150px;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.header p {
  width: 80%;
  overflow-wrap: break-word;
  text-align: center;
  margin: auto;
}
.nav {
  display: flex;
  height: 50px;
  width: 80%;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #000;
  margin: 0 auto;
}

.nav-btn {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-basis: 30%;
}

.nav-btn li:hover {
  border-bottom: 3px solid #000;
}
.projectTeam {
  width: 80%;

  margin: auto;
}

.projectTeam h2{
  text-align: center;
}

.projectTeam span {
  font-size: 1rem;
}

.memberOfTeam {
  display: flex;
  margin: 0.7rem 0;
}

.memberOfTeam img {
  margin-right: 1rem;
  border-radius: 50%;
}

.projectDescription {
  margin: auto;
  width: 80%;
}

.projectDescription p {
  margin: 2rem auto;
  width: 40%;

  text-align: center;
  font-size: 1.3rem;
}

.authorOfDay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin: 2rem auto;
}


.search-form {
    margin: 20px auto;
    text-align: center;
}

.search-form label{
    margin: 0 20px;;
}
.search-list {
    width: 80%;
    margin: 0 auto;
  }
  
  .search-item {
    display: flex;
    align-items: center;
    border: 1px solid gray;
    border-radius: 10px;
    margin: 5px;
    padding: 5px;
    cursor: pointer;
  }
  
  .search-item:hover{
    background-color: lightgray;
  }
  
  .search-item img{
    width: 20%;
    height: 20%;
    min-width: 100px;
    min-height: 100px;
    border-radius: 10px;
  }
  
  .search-info {
    margin-left: 10px;
    width: 100%;
    text-align: center;
  }
  
  .search-name {
    text-decoration: underline;
  }
  
  .search-activity {
    margin: 0;
  }
.person-info {
  margin: 30px auto;
  width: 30%;
  height: 40%;
  text-align: center;
}

.person-info img{
  width: 100%;
  height: 100%;
  min-width: 100px;
  min-height: 100px;
	border-radius: 100%;
}
.image-gallery {
    width: 60%;
    margin: 0 auto;
}


@-webkit-keyframes modal-video {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes modal-video {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes modal-video-inner {
  from {
    -webkit-transform: translate(0, 100px);
            transform: translate(0, 100px); }
  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); } }

@keyframes modal-video-inner {
  from {
    -webkit-transform: translate(0, 100px);
            transform: translate(0, 100px); }
  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); } }

.modal-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000000;
  cursor: pointer;
  opacity: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-name: modal-video;
          animation-name: modal-video;
  transition: opacity 0.3s ease-out; }

.modal-video-effect-exit {
  opacity: 0; }
  .modal-video-effect-exit .modal-video-movie-wrap {
    -webkit-transform: translate(0, 100px);
    transform: translate(0, 100px); }

.modal-video-body {
  max-width: 940px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: table; }

.modal-video-inner {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%; }

.modal-video-movie-wrap {
  width: 100%;
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
  background-color: #333;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-name: modal-video-inner;
          animation-name: modal-video-inner;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out; }
  .modal-video-movie-wrap iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.modal-video-close-btn {
  position: absolute;
  z-index: 2;
  top: -35px;
  right: -35px;
  display: inline-block;
  width: 35px;
  height: 35px;
  overflow: hidden;
  border: none;
  background: transparent; }
  .modal-video-close-btn:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .modal-video-close-btn:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  .modal-video-close-btn:before, .modal-video-close-btn:after {
    content: '';
    position: absolute;
    height: 2px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #fff;
    border-radius: 5px;
    margin-top: -6px; }

.video-overlay {
    width: 30%;
    margin: 20px auto;
    text-align: center;
}
.marker {
  border-radius: 50%;
  background-color: red;
  font-size: 14px;
  width: 20px;
  height: 20px;
  position: absolute;
  left: -10px;
  top: -10px;
}

.map {
  height: 50vh;
  width: 60%;
  margin: 30px auto;
}
.sorry-message {
  text-align: center;
}
.footer {
	width: 100%;
	text-align: center;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.footer-border-span {
	width: 80%;
	margin: 40px auto 0 auto;
	border-top: 1px solid black;
	padding-top: 20px;
	margin-top: 40px;
}
